(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .service('Trucks', TruckServices);

  /** @ngInject */
  function TruckServices($q, $http, Config, Upload) {
	var vm = this;
	vm.api_endpoint_trucks = Config.ENV.API_ENDPOINT + 'trucks/';

	vm.trucksForCarrier = function(params)
	{
		// Defer
        var defer = $q.defer();
        // Url
        var url = vm.api_endpoint_trucks + 'trucksForCarrier';

        // Get
        $http.post(url, params)
            .then(function(response) {
                // Success
                if(response.data.status == 'ok' ){
                	defer.resolve(response.data.message);	
                }else{
					defer.reject(false);                	
                }
            })
            .catch(function(response) {
                // Error

                defer.reject(false);
            });

        return defer.promise;
	};

  vm.trucksDetails = function(params)
  {
    // Defer
        var defer = $q.defer();
        // Url
        var url = vm.api_endpoint_trucks + 'DetailTruck';

        // Get
        $http.post(url, params)
            .then(function(response) {
                // Success
                if(response.data.status == 'ok' ){
                  defer.resolve(response.data.message); 
                }else{
          defer.reject(false);                  
                }
            })
            .catch(function(response) {
                // Error

                defer.reject(false);
            });

        return defer.promise;
  };

  vm.createTruck = function(params)
  {
    // Defer
        var defer = $q.defer();
        // Url
        var url = vm.api_endpoint_trucks + 'createTruck';

        // Get
        Upload.upload({url: url, data: params})
          .then(function(response) {
                // Success
                console.log(response);
                if(response.data.status == 'ok' ){
                  defer.resolve(response.data.message); 
                }else{
                  defer.reject(false);                   
                }
            })
            .catch(function(response) {
                // Error

                defer.reject(false);
            });

        return defer.promise;
  };

  vm.updateTruck = function(params, withImg)
  {
    // Defer
    var defer = $q.defer();
    // Url
    var url = vm.api_endpoint_trucks + 'UpdateTruck';

    if(withImg){
      // Get
      Upload.upload({url: url, data: params})
        .then(function(response) {
              // Success
              //Upload.abort();
              console.log(response);
              if(response.data.status == 'ok' ){
                defer.resolve(response.data.message); 
              }else{
                defer.reject(false);                   
              }
          })
          .catch(function(response) {
              // Error

              defer.reject(false);
          });
    }else{
      console.log(params);
      $http.post(url, params)
          .then(function(response) {
              // Success
              if(response.data.status == 'ok' ){
                defer.resolve(response.data.message); 
              }else{
                defer.reject(false);                  
              }
          })
          .catch(function(response) {
              // Error

              defer.reject(false);
          });
    }

    return defer.promise;
  };

  vm.searchInterestPoints = function(params)
  {
    // Defer
        var defer = $q.defer();
        // Url
        var url = vm.api_endpoint_trucks + 'PointsOfInterest';

        // Get
        $http.post(url, params)
            .then(function(response) {
                // Success
                if(response.data.status == 'ok' ){
                  defer.resolve(response.data.message); 
                }else{
                  defer.reject(false);                  
                }
            })
            .catch(function(response) {
                // Error

                defer.reject(false);
            });

        return defer.promise;
  };

  vm.countTrucksxCarrier = function(params)
  {
    // Defer
        var defer = $q.defer();
        // Url
        var url = vm.api_endpoint_trucks + 'CountTruckxCarrier';

        // Get
        $http.post(url, params)
            .then(function(response) {
                // Success
                if(response.data.status == 'ok' ){
                  defer.resolve(response.data.message); 
                }else{
          defer.reject(false);                  
                }
            })
            .catch(function(response) {
                // Error

                defer.reject(false);
            });

        return defer.promise;
  };


  }
})();

(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .directive('truckDetailsRow', truckDetailsRow);

  /** @ngInject */
  function truckDetailsRow($rootScope, Auth, Trucks, uiGmapGoogleMapApi, $uibModal) {
    return {
        templateUrl: 'app/trucks/templates/directives/truck-details-row.html',
        restrict: 'E',
        replace: true,
        scope: {
          truckId: '='
        },
        link: function(scope) {
            scope.truckData = {};
            scope.loadingTruckData = true;
            /**
             * Initialize
             */
            scope.initialize = function() {
                scope.truckOptions = {
                  'token': Auth.getUserToken(),
                  'id': scope.truckId
                };

                scope.getTruckDetails();
            };

            scope.getTruckDetails =  function() {
              Trucks.trucksDetails(scope.truckOptions)
                .then(function(response){
                  scope.truckData = response.Truck_Details[0];
                  if(angular.isDefined(scope.truckData.skills_truck) && scope.truckData.skills_truck.length){
                    var arr_skills = [];
                    for(var sk in scope.truckData.skills_truck){
                      arr_skills.push(scope.truckData.skills_truck[sk].skills_name);
                    }
                    scope.truckData.SkillsTruckText = arr_skills.join(', ');
                  }else{
                    scope.truckData.SkillsTruckText = "---";
                  }

                  if(angular.isDefined(scope.truckData.truck_availability) && scope.truckData.truck_availability.length){
                    var arr_availability = [];
                    for(var av in scope.truckData.truck_availability){
                      arr_availability.push(scope.truckData.truck_availability[av].availablity_name);
                    }
                    scope.truckData.AvailabilityTruckText = arr_availability.join(', ');
                  }else{
                    scope.truckData.AvailabilityTruckText = "---";
                  }
                  scope.loadingTruckData = false;
                })
                .catch(function (error) {
                  scope.loadingTruckData = false;
                });
            };

            // Initialize
            scope.initialize();
        }
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .controller('TrucksEditController', TrucksEditController);

  /** @ngInject */
  function TrucksEditController($state, $stateParams, toastr, $filter, Config, Trucks, Loads, Carriers, Auth, DatatableTool, $q) {
	  var vm = this;

	  vm.initialize = function() {
      vm.submitReturnList = false;
      vm.loadingTruckDetails = true;
      vm.truckId = $stateParams.id|| false;
      vm.truckData = false;

      vm.dataUpdateTruck = {};
      vm.dataUpdateTruck.id_truck = null;
      vm.dataUpdateTruck.token = Auth.getUserToken();
      vm.dataUpdateTruck.brand = null;
      vm.dataUpdateTruck.model = null;
      vm.dataUpdateTruck.year = null;
      vm.dataUpdateTruck.plate = null;
      vm.dataUpdateTruck.id_driver = null;
      vm.dataUpdateTruck.id_truck_type = null;
      vm.dataUpdateTruck.length = null;
      vm.dataUpdateTruck.weight = null,
      vm.dataUpdateTruck.id_capacity_size = null;
      vm.dataUpdateTruck.max_distance = null;
      vm.dataUpdateTruck.additional_info = null;
      vm.dataUpdateTruck.pic = null;
      vm.dataUpdateTruck.skils_truck = [];
      vm.dataUpdateTruck.availablity = [];

      vm.savingTruck = false;

      vm.getItemsSizeLengthLoads();
      vm.getMyDrivers();

      vm.truckOptions = {
        'token': Auth.getUserToken(),
        'id': vm.truckId
      };

    };

    vm.getMyDrivers = function(){
      var params = {};
      params.token = Auth.getUserToken();
      vm.myDriversList = [];

      Carriers.getDriverxCarrier(params)
        .then(function(response){
          if(response.list_drivers){
            vm.myDriversList = response.list_drivers;
          }
        });      
    };

    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.TrucksSkills_list)){
              vm.setTruckSkillList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.TruckAvailability_list)){
              vm.setTruckAvailabilityList();
            }

            vm.getDataTruck();
          }
        }); 
    };

    vm.getDataTruck =  function() {
      Trucks.trucksDetails(vm.truckOptions)
        .then(function(response){
          console.log(response);
          vm.truckData = response.Truck_Details[0];
          vm.setDataToForm();

          vm.loadingTruckDetails = false;
        })
        .catch(function (error) {
          vm.loadingTruckDetails = false;
        });
    };

    vm.setTruckSkillList = function() {
      vm.skillList = [];
      if(vm.itemsSizeLenLoads.TrucksSkills_list.length){
        for(var i in vm.itemsSizeLenLoads.TrucksSkills_list){
          vm.skillList.push({id: vm.itemsSizeLenLoads.TrucksSkills_list[i].id, name: vm.itemsSizeLenLoads.TrucksSkills_list[i].skills_name, checked: false});
        }
      }
    };

    vm.setTruckAvailabilityList = function() {
      vm.availabilityList = [];
      if(vm.itemsSizeLenLoads.TruckAvailability_list.length){
        for(var i in vm.itemsSizeLenLoads.TruckAvailability_list){
          vm.availabilityList.push({id: vm.itemsSizeLenLoads.TruckAvailability_list[i].id, name: vm.itemsSizeLenLoads.TruckAvailability_list[i].availablity_name, checked: false});
        }
      }
    };

    vm.setDataToForm = function() {
      vm.dataUpdateTruck.id_truck = (angular.isDefined(vm.truckData.id)) ? vm.truckData.id : vm.truckId;
      vm.dataUpdateTruck.brand = (angular.isDefined(vm.truckData.brand)) ? vm.truckData.brand : null;
      vm.dataUpdateTruck.model = (angular.isDefined(vm.truckData.model)) ? vm.truckData.model : null;
      vm.dataUpdateTruck.year = (angular.isDefined(vm.truckData.year)) ? parseInt(vm.truckData.year) : null;
      vm.dataUpdateTruck.plate = (angular.isDefined(vm.truckData.plate)) ? vm.truckData.plate : null;
      vm.dataUpdateTruck.id_driver = (angular.isDefined(vm.truckData.truck_driver)) ? vm.truckData.truck_driver.toString() : null;
      vm.dataUpdateTruck.id_truck_type = (angular.isDefined(vm.truckData.truck_type.id)) ? vm.truckData.truck_type.id.toString() : null;
      vm.dataUpdateTruck.length = (angular.isDefined(vm.truckData.length)) ? parseInt(vm.truckData.length) : null;
      vm.dataUpdateTruck.weight = (angular.isDefined(vm.truckData.weight)) ? parseInt(vm.truckData.weight) : null;
      vm.dataUpdateTruck.id_capacity_size = (angular.isDefined(vm.truckData.truck_capacity.id)) ? vm.truckData.truck_capacity.id.toString() : null;
      vm.dataUpdateTruck.max_distance = (angular.isDefined(vm.truckData.max_distance)) ? parseInt(vm.truckData.max_distance) : null;
      vm.dataUpdateTruck.additional_info = (angular.isDefined(vm.truckData.additional_info)) ? vm.truckData.additional_info : null;
      vm.dataUpdateTruck.pic = (angular.isDefined(vm.truckData.imagenes_trucks) && vm.truckData.imagenes_trucks.length) ? vm.truckData.imagenes_trucks[0]['imagen-270'] : null;

      if(angular.isDefined(vm.truckData.skills_truck) && vm.truckData.skills_truck.length > 0){
        vm.dataUpdateTruck.skils_truck = [];
        vm.truckData.skills_truck.forEach(function(item) {
          vm.dataUpdateTruck.skils_truck.push(item.id);
        });

        if(angular.isDefined(vm.skillList) && vm.skillList.length){
          vm.skillList.forEach(function(item) {
            if(vm.dataUpdateTruck.skils_truck.indexOf(item.id) != -1){
              item.checked = true;
            }
          });
        }
      }

      if(angular.isDefined(vm.truckData.truck_availability) && vm.truckData.truck_availability.length > 0){
        vm.dataUpdateTruck.availablity = [];
        vm.truckData.truck_availability.forEach(function(item) {
          vm.dataUpdateTruck.availablity.push(item.id);
        });

        if(angular.isDefined(vm.availabilityList) && vm.availabilityList.length){
          vm.availabilityList.forEach(function(item) {
            if(vm.dataUpdateTruck.availablity.indexOf(item.id) != -1){
              item.checked = true;
            }
          });
        }
      }
    };

    vm.save = function(postTruckFormName) {
      vm.savingTruck = true;
      var withImg = true;
      // Validate form.
      if (postTruckFormName.$invalid) {
          // If there was an error for required fields.
          if (postTruckFormName.$error.required) {
              toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
          }
          vm.savingTruck = false;
          return;
      }

      if(angular.isDefined(vm.skillList)){
        vm.dataUpdateTruck.skils_truck = [];
        vm.skillList.forEach(function(item) {
          if (item.checked) vm.dataUpdateTruck.skils_truck.push(item.id);
        });

        vm.dataUpdateTruck.skils_truck = JSON.stringify(vm.dataUpdateTruck.skils_truck);
      }

      if(angular.isDefined(vm.availabilityList)){
        vm.dataUpdateTruck.availablity = [];
        vm.availabilityList.forEach(function(item) {
          if (item.checked) vm.dataUpdateTruck.availablity.push(item.id);
        });

        vm.dataUpdateTruck.availablity = JSON.stringify(vm.dataUpdateTruck.availablity);
      }

      if(typeof vm.dataUpdateTruck.pic == 'string' || vm.dataUpdateTruck.pic == null){
        delete vm.dataUpdateTruck.pic;
        withImg = false;
      }

      Trucks.updateTruck(vm.dataUpdateTruck, withImg)
            .then(function(response) {
                // Success
                toastr.success($filter('translate')('trucks:edit:form:success'));
                vm.getDataTruck();
                vm.savingTruck = false;
            })
            .catch(function(error) {
                // Error
                if(error === 'image_exceeded_limit'){
                  toastr.error($filter('translate')('trucks:add:form:pic_size:over'));
                }else{
                  toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                }
                // Loading
                vm.savingTruck = false;
            });

    };

    vm.submitReturnListFunc = function(postTruckFormName) {
      vm.submitReturnList = true;
      vm.save(postTruckFormName);
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .controller('TrucksController', TrucksController);

  /** @ngInject */
  function TrucksController($state, $stateParams, toastr, $filter, Config, Trucks, Auth, DatatableTool, DTOptionsBuilder, DTColumnBuilder, $sce) {
	  var vm = this;

	  vm.initialize = function() {
      var search_type = '';
      vm.profile = Auth.getUserProfile();

      switch(vm.profile){
        case 'CARRIER':
          search_type = 'all';
          break;
      }

      vm.truckOptions = {
        'shortcut': (search_type != '') ? search_type : 'all',
        'token': Auth.getUserToken(),
        'page': 1
      };
      vm.trucksList = [];
      vm.loadingTruckData = true;
      vm.initializedList = false;

      vm.loadTab();  
    };

    vm.loadTab = function() {
      if (vm.initializedList) {
        vm.reloadList();
      } else {
        vm.initializeList();
      }
    };

    vm.changeCategory = function(cat) {
      vm.loadingTruckData = true;
      vm.truckOptions.shortcut = cat;
      vm.truckOptions.page = 1;

      vm.loadTab();
    };

    vm.initializeList = function() {
      DatatableTool.setLimit(20);
      vm.datatableOptions = DTOptionsBuilder.newOptions()
                            .withOption('ajax', function(data, callback, settings){
                              // Page
                              vm.truckOptions.page = DatatableTool.getPage(data);
                              console.log(vm.truckOptions.page);

                              Trucks.trucksForCarrier(vm.truckOptions)
                                .then(function(response){
                                  var records = DatatableTool.getRecordsTrucks(response);

                                  // Info
                                  if (!angular.isUndefined(records.data)) {
                                      _.forEach(records.data, function(value, key) {

                                          value.truck_img = false;
                                          console.log(value.img_truck_imagenes);
                                          if(angular.isDefined(value.img_truck_imagenes) && value.img_truck_imagenes != null){
                                            value.truck_img = value.img_truck_imagenes[0]['imagen-60'];
                                          }

                                          value.truck_name = value.brand+' '+value.marca+' '+value.model;

                                          value.driver_name = value.driver.user.user_first_name+' '+value.driver.user.user_last_name;

                                          value.truck_status = '';
                                            var bgColorBtn = ''
                                            , colorBtn = '';

                                            switch(value.status){
                                              case '1':
                                                bgColorBtn = 'bg-color-3';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case '0':
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                            }

                                            value.truck_status = $sce.trustAsHtml('<button class="btn btn-class2 '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('trucks:table:status:' +value.status+ ':text')+'</button>');

                                          value.actions = '<i ng-click="trucks.showDetailsUnique('+value.id+')" class="fa fa-angle-down show-load-details-btn"></i>';
                                      });
                                  }
                                  vm.trucksList = records.data;
                                  records.data = [{truck_img: '', truck_full_name: '', truck_status: '', actions: ''}];
                                  callback(records);
                                })
                                .catch(function (error) {
                                  vm.trucksList = [];
                                  callback(DatatableTool.getRecords(null));
                                });
                            })
                            .withDataProp('data')
                            .withOption('processing', true)
                            .withOption('serverSide', true)
                            .withOption('searching', false)
                            .withOption('info', false)
                            .withOption('lengthChange', false)
                            .withOption('pageLength', DatatableTool.getLimit(null))
                            .withOption('responsive', true)
                            .withLanguage(DatatableTool.getDefaultLanguageSettings())
                            .withPaginationType('numbers');
      switch(vm.profile){
        case 'CARRIER':
          vm.dtColumns = [
            DTColumnBuilder.newColumn('truck_img').withTitle(''),
            DTColumnBuilder.newColumn('truck_full_name').withTitle(''),
            DTColumnBuilder.newColumn('truck_status').withTitle(''),
            DTColumnBuilder.newColumn('actions').withTitle('')
          ];
          break;
      }

      vm.loadingTruckData = false;
      vm.initializedList = true;
    };

    vm.reloadList = function() {
        // Reload datatables
        DatatableTool.reload();
    };

    vm.showDetailsUnique = function(truck_id) {
      for(var i in vm.trucksList){
        if(vm.trucksList[i].id == truck_id){
          vm.trucksList[i].expanded = true;
        }else{
          vm.trucksList[i].expanded = false;
        }
      }
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .controller('TrucksAddController', TrucksAddController);

  /** @ngInject */
  function TrucksAddController($state, toastr, $filter, Config, Trucks, Loads, Carriers, Auth, DatatableTool, $q) {
	  var vm = this;

	  vm.initialize = function() {
      vm.submitReturnList = false;

      vm.dataNewTruck = {};
      vm.dataNewTruck.token = Auth.getUserToken();
      vm.dataNewTruck.brand = null;
      vm.dataNewTruck.model = null;
      vm.dataNewTruck.year = null;
      vm.dataNewTruck.plate = null;
      vm.dataNewTruck.id_driver = null;
      vm.dataNewTruck.id_truck_type = null;
      vm.dataNewTruck.length = null;
      vm.dataNewTruck.weight = null,
      vm.dataNewTruck.id_capacity_size = null;
      vm.dataNewTruck.max_distance = null;
      vm.dataNewTruck.additional_info = null;
      vm.dataNewTruck.skils_truck = [];
      vm.dataNewTruck.availablity = [];

      vm.savingNewTruck = false;

      vm.getItemsSizeLengthLoads();
      vm.getMyDrivers();

    };

    vm.getMyDrivers = function(){
      var params = {};
      params.token = Auth.getUserToken();
      vm.myDriversList = [];

      Carriers.getDriverxCarrier(params)
        .then(function(response){
          if(response.list_drivers){
            vm.myDriversList = response.list_drivers;
          }
        });      
    };

    vm.getItemsSizeLengthLoads = function() {
      vm.itemsSizeLenLoads = {};
      Loads.getItemsSizeLengthLoads()
        .then(function(response){
          if(response){
            console.log(response);
            vm.itemsSizeLenLoads = response;
            if(angular.isDefined(vm.itemsSizeLenLoads.TrucksSkills_list)){
              vm.setTruckSkillList();
            }

            if(angular.isDefined(vm.itemsSizeLenLoads.TruckAvailability_list)){
              vm.setTruckAvailabilityList();
            }
          }
        }); 
    };

    vm.setTruckSkillList = function() {
      vm.skillList = [];
      if(vm.itemsSizeLenLoads.TrucksSkills_list.length){
        for(var i in vm.itemsSizeLenLoads.TrucksSkills_list){
          vm.skillList.push({id: vm.itemsSizeLenLoads.TrucksSkills_list[i].id, name: vm.itemsSizeLenLoads.TrucksSkills_list[i].skills_name, checked: false});
        }
      }
    };

    vm.setTruckAvailabilityList = function() {
      vm.availabilityList = [];
      if(vm.itemsSizeLenLoads.TruckAvailability_list.length){
        for(var i in vm.itemsSizeLenLoads.TruckAvailability_list){
          vm.availabilityList.push({id: vm.itemsSizeLenLoads.TruckAvailability_list[i].id, name: vm.itemsSizeLenLoads.TruckAvailability_list[i].availablity_name, checked: false});
        }
      }
    };

    vm.save = function(postTruckFormName) {
      vm.savingNewTruck = true;
      // Validate form.
      if (postTruckFormName.$invalid) {
          // If there was an error for required fields.
          if (postTruckFormName.$error.required) {
              toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
          }
          vm.savingNewTruck = false;
          return;
      }

      if(angular.isDefined(vm.skillList)){
        vm.skillList.forEach(function(item) {
          if (item.checked) vm.dataNewTruck.skils_truck.push(item.id);
        });
      }

      if(angular.isDefined(vm.availabilityList)){
        vm.availabilityList.forEach(function(item) {
          if (item.checked) vm.dataNewTruck.availablity.push(item.id);
        });
      }

      Trucks.createTruck(vm.dataNewTruck)
            .then(function(response) {
                // Success
                console.log(response);
                toastr.success($filter('translate')('trucks:add:form:success'));
                if(vm.submitReturnList){
                  $state.go('private.trucks');
                }else{
                  vm.initialize();
                }
                vm.savingNewTruck = false;
            })
            .catch(function(error) {
                // Error
                if(error === 'image_exceeded_limit'){
                  toastr.error($filter('translate')('trucks:add:form:pic_size:over'));
                }else{
                  toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                }
                // Loading
                vm.savingNewTruck = false;
            });

    };

    vm.submitReturnListFunc = function(postTruckFormName) {
      vm.submitReturnList = true;
      vm.save(postTruckFormName);
    };

    vm.initialize();
  }
})();

