(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .controller('DriversController', DriversController);

  /** @ngInject */
  function DriversController($state, $stateParams, toastr, $filter, Config, Drivers, Auth, DatatableTool, DTOptionsBuilder, DTColumnBuilder, $sce) {
	  var vm = this;

	  vm.initialize = function() {
      var search_type = '';
      vm.profile = Auth.getUserProfile();

      switch(vm.profile){
        case 'CARRIER':
          search_type = 'all';
          break;
      }

      vm.driverOptions = {
        'shortcut': (search_type != '') ? search_type : 'all',
        'token': Auth.getUserToken(),
        'page': 1
      };
      vm.driversList = [];
      vm.loadingDriverData = true;
      vm.initializedList = false;

      vm.loadTab();  
    };

    vm.loadTab = function() {
      if (vm.initializedList) {
        vm.reloadList();
      } else {
        vm.initializeList();
      }
    };

    vm.changeCategory = function(cat) {
      vm.loadingDriverData = true;
      vm.driverOptions.shortcut = cat;
      vm.driverOptions.page = 1;

      vm.loadTab();
    };

    vm.initializeList = function() {
      DatatableTool.setLimit(20);
      vm.datatableOptions = DTOptionsBuilder.newOptions()
                            .withOption('ajax', function(data, callback, settings){
                              // Page
                              vm.driverOptions.page = DatatableTool.getPage(data);
                              console.log(vm.driverOptions.page);

                              Drivers.driversForCarrier(vm.driverOptions)
                                .then(function(response){
                                  var records = DatatableTool.getRecordsDrivers(response);

                                  // Info
                                  if (!angular.isUndefined(records.data)) {
                                      _.forEach(records.data, function(value, key) {

                                          value.driver_img = false;
                                          if(angular.isDefined(value.user.img_perfil_arr) && value.user.img_perfil_arr != null){
                                            value.driver_img = value.user.img_perfil_arr[0]['imagen-60'];
                                          }

                                          value.driver_name = value.user.user_first_name+' '+value.user.user_last_name;

                                          value.driver_details = value.user.user_email+' '+value.user.user_phone;

                                          value.driver_status = '';
                                            var bgColorBtn = ''
                                            , colorBtn = '';

                                            switch(value.user.user_status){
                                              case '1':
                                                bgColorBtn = 'bg-color-3';
                                                colorBtn = 'color-armadillo';
                                                break;
                                              case '0':
                                                bgColorBtn = 'bg-color-7';
                                                colorBtn = 'color-armadillo';
                                                break;
                                            }

                                            value.driver_status = $sce.trustAsHtml('<button class="btn btn-class2 '+bgColorBtn+' '+colorBtn+'">'+$filter('translate')('trucks:table:status:' +value.user.user_status+ ':text')+'</button>');

                                          value.actions = '<i ng-click="drivers.showDetailsUnique('+value.user_id+')" class="fa fa-angle-down show-load-details-btn"></i>';
                                      });
                                  }
                                  vm.driversList = records.data;
                                  records.data = [{driver_img: '', driver_full_name: '', driver_status: '', actions: ''}];
                                  callback(records);
                                })
                                .catch(function (error) {
                                  vm.driversList = [];
                                  callback(DatatableTool.getRecords(null));
                                });
                            })
                            .withDataProp('data')
                            .withOption('processing', true)
                            .withOption('serverSide', true)
                            .withOption('searching', false)
                            .withOption('info', false)
                            .withOption('lengthChange', false)
                            .withOption('pageLength', DatatableTool.getLimit(null))
                            .withOption('responsive', true)
                            .withLanguage(DatatableTool.getDefaultLanguageSettings())
                            .withPaginationType('numbers');
      switch(vm.profile){
        case 'CARRIER':
          vm.dtColumns = [
            DTColumnBuilder.newColumn('driver_img').withTitle(''),
            DTColumnBuilder.newColumn('driver_full_name').withTitle(''),
            DTColumnBuilder.newColumn('driver_status').withTitle(''),
            DTColumnBuilder.newColumn('actions').withTitle('')
          ];
          break;
      }

      vm.loadingDriverData = false;
      vm.initializedList = true;
    };

    vm.reloadList = function() {
        // Reload datatables
        DatatableTool.reload();
    };

    vm.showDetailsUnique = function(driver_id) {
      console.log(driver_id);
      for(var i in vm.driversList){
        if(vm.driversList[i].user_id == driver_id){
          vm.driversList[i].expanded = true;
        }else{
          vm.driversList[i].expanded = false;
        }
      }
    };

    vm.initialize();
  }
})();

(function() {
  'use strict';

  angular
    .module('loadsAppWeb')
    .controller('DriversAddController', DriversAddController);

  /** @ngInject */
  function DriversAddController($state, toastr, $filter, Config, Drivers, Loads, Carriers, Auth, DatatableTool, $q) {
	  var vm = this;

	  vm.initialize = function() {
      vm.submitReturnList = false;

      vm.dataNewDriver = {};
      vm.dataNewDriver.profile = 'DRIVER';
      vm.dataNewDriver.token = Auth.getUserToken();
      vm.dataNewDriver.user_first_name = null;
      vm.dataNewDriver.user_last_name = null;
      vm.dataNewDriver.user_phone = null;
      vm.dataNewDriver.user_email = null;
      vm.dataNewDriver.user_contry = null;
      vm.dataNewDriver.user_password = null;
      vm.dataNewDriver.user_confirmpassword = null;
      vm.dataNewDriver.bond_insurance_num_policy = null;
      vm.dataNewDriver.bond_insurance_comp = null,
      vm.dataNewDriver.bond_insurance_num = null;
      vm.dataNewDriver.user_postalcode = null;
      vm.dataNewDriver.name_bank = null;
      vm.dataNewDriver.number_account_bank = null;
      vm.dataNewDriver.user_city = null;
      vm.dataNewDriver.user_state = null;
      vm.dataNewDriver.user_postalcode = null;
      vm.dataNewDriver.driver_license_num = null;

      vm.savingNewDriver = false;

    };

    vm.save = function(postDriverFormName) {
      vm.savingNewDriver = true;
      // Validate form.
      if (postDriverFormName.$invalid) {
          // If there was an error for required fields.
          if (postDriverFormName.$error.required) {
              toastr.error($filter('translate')('loads:add:form:required:fields:error'));
          } else {
              toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
          }
          vm.savingNewDriver = false;
          return;
      }

      if(vm.dataNewDriver.user_password != vm.dataNewDriver.user_confirmpassword){
        toastr.error($filter('translate')('drivers:add:form:password:validate'));
        vm.savingNewDriver = false;
        return;
      }

      Drivers.createDriver(vm.dataNewDriver)
            .then(function(response) {
                // Success
                console.log(response);
                toastr.success($filter('translate')('drivers:add:form:success'));
                if(vm.submitReturnList){
                  $state.go('private.drivers');
                }else{
                  vm.initialize();
                }
                vm.savingNewDriver = false;
            })
            .catch(function(error) {
                // Error
                if(error === 'image_exceeded_limit'){
                  toastr.error($filter('translate')('drivers:add:form:pic_size:over'));
                }else{
                  toastr.error($filter('translate')('loads:add:form:required:fields:invalid'));
                }
                // Loading
                vm.savingNewDriver = false;
            });

    };

    vm.submitReturnListFunc = function(postDriverFormName) {
      vm.submitReturnList = true;
      vm.save(postDriverFormName);
    };

    vm.initialize();
  }
})();

